  align-items: center;
  display: flex;
}

.loader {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  position: absolute;
}

.spinner {
  width: 5px;
  height: 5px;
  background-color: #0ff;
  border-radius: 50%;
  box-shadow: 0 0 10px .5px #0ff;
}

.loader:first-child {
  animation: load1 2s linear 1.1s infinite;
  transform: rotate(120deg);
}

.loader:nth-child(2) {
  animation: load2 2s linear 1.2s infinite;
  transform: rotate(90deg);
}

.loader:nth-child(3) {
  animation: load3 2s linear 1.3s infinite;
  transform: rotate(60deg);
}

.loader:nth-child(4) {
  animation: load4 2s linear 1.4s infinite;
  transform: rotate(30deg);
}

.loader:nth-child(5) {
  animation: load5 2s linear 1.5s infinite;
  transform: rotate(10deg);
}

.loader:nth-child(6) {
  animation: load6 2s linear 1.6s infinite;
}

.loader:nth-child(7) {
  animation: load7 2s linear 1.7s infinite;
}

.loader:last-child {
  animation: load8 2s linear 1.8s infinite;
}

@keyframes load1 {
  75% {
    opacity: 0;
  }

  85% {
    opacity: .3;
  }

  100% {
    opacity: 1;
    transform: rotate(339deg);
  }
}

@keyframes load2 {
  75% {
    opacity: 0;
  }

  85% {
    opacity: .3;
  }

  100% {
    opacity: 1;
    transform: rotate(342deg);
  }
}

@keyframes load3 {
  75% {
    opacity: 0;
  }

  85% {
    opacity: .3;
  }

  100% {
    opacity: 1;
    transform: rotate(345deg);
  }
}

@keyframes load4 {
  75% {
    opacity: 0;
  }

  85% {
    opacity: .3;
  }

  100% {
    opacity: 1;
    transform: rotate(348deg);
  }
}

@keyframes load5 {
  50% {
    opacity: 0;
  }

  100% {
    opacity: 0;
    transform: rotate(351deg);
  }
}

@keyframes load6 {
  50% {
    opacity: 0;
  }

  100% {
    opacity: 0;
    transform: rotate(354deg);
  }
}

@keyframes load7 {
  50% {
    opacity: 0;
  }

  100% {
    opacity: 0;
    transform: rotate(357deg);
  }
}

@keyframes load8 {
